import { lazy, useMemo } from "react";
import { useParams, useHistory } from "react-router-dom";
import { encodeObj, decodeObj } from "../../utils";

const activities = {
    question: lazy(() => import("./Question")),
    redirect: props => {
        window.location = props.url;
    },
};

const Game = () => {
    const { code } = useParams();
    const history = useHistory();
    const [step, ...next] = useMemo(() => decodeObj(code), [code]);

    const Component = activities[step.type];

    const handleDone = () => {
        const url = encodeObj(next);
        history.push(`/${url}`);
    };

    return <Component {...step} onDone={handleDone} />;
};

export default Game;
